import React, { useState } from 'react';
import { useAppDispatch } from '../../hooks/useAppDispatch';
import { createList, deleteList } from '../../store/slices/listsSlice';
import styles from './CustomListManager.module.css';

/**
 * –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –¥–ª—è —É–ø—Ä–∞–≤–ª–µ–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫–∏–º–∏ —Å–ø–∏—Å–∫–∞–º–∏
 * –ü–æ–∑–≤–æ–ª—è–µ—Ç —Å–æ–∑–¥–∞–≤–∞—Ç—å –∏ —É–¥–∞–ª—è—Ç—å —Å–ø–∏—Å–∫–∏
 */
const CustomListManager = ({ lists, mediaType, onListSelect }) => {
  const dispatch = useAppDispatch();
  const [isCreating, setIsCreating] = useState(false);
  const [newListName, setNewListName] = useState('');
  const [error, setError] = useState(null);

  // –§–∏–ª—å—Ç—Ä—É–µ–º —Å–ø–∏—Å–∫–∏ –ø–æ —Ç–∏–ø—É –º–µ–¥–∏–∞
  const filteredLists = lists.filter(list => list.mediaType === mediaType);

  // –°–æ–∑–¥–∞–Ω–∏–µ –Ω–æ–≤–æ–≥–æ —Å–ø–∏—Å–∫–∞
  const handleCreateList = async (e) => {
    e.preventDefault();
    
    if (!newListName.trim()) {
      setError('–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ —Å–ø–∏—Å–∫–∞');
      return;
    }

    try {
      await dispatch(createList({ 
        name: newListName.trim(), 
        mediaType 
      })).unwrap();
      
      setNewListName('');
      setIsCreating(false);
      setError(null);
    } catch (err) {
      setError(err.message || '–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ —Å–ø–∏—Å–∫–∞');
    }
  };

  // –£–¥–∞–ª–µ–Ω–∏–µ —Å–ø–∏—Å–∫–∞
  const handleDeleteList = async (listId, listName) => {
    if (!window.confirm(`–£–¥–∞–ª–∏—Ç—å —Å–ø–∏—Å–æ–∫ "${listName}"? –í—Å–µ —ç–ª–µ–º–µ–Ω—Ç—ã –±—É–¥—É—Ç —É–¥–∞–ª–µ–Ω—ã.`)) {
      return;
    }

    try {
      await dispatch(deleteList(listId)).unwrap();
    } catch (err) {
      setError(err.message || '–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ —Å–ø–∏—Å–∫–∞');
    }
  };

  // –û—Ç–º–µ–Ω–∞ —Å–æ–∑–¥–∞–Ω–∏—è
  const handleCancelCreate = () => {
    setIsCreating(false);
    setNewListName('');
    setError(null);
  };

  return (
    <div className={styles.listManager}>
      <div className={styles.header}>
        <h2 className={styles.title}>
          {mediaType === 'movie' ? '–°–ø–∏—Å–∫–∏ —Ñ–∏–ª—å–º–æ–≤' : '–°–ø–∏—Å–∫–∏ —Å–µ—Ä–∏–∞–ª–æ–≤'}
        </h2>
        
        {!isCreating && (
          <button 
            className={styles.createButton}
            onClick={() => setIsCreating(true)}
            title="–°–æ–∑–¥–∞—Ç—å –Ω–æ–≤—ã–π —Å–ø–∏—Å–æ–∫"
          >
            + –°–æ–∑–¥–∞—Ç—å —Å–ø–∏—Å–æ–∫
          </button>
        )}
      </div>

      {/* –§–æ—Ä–º–∞ —Å–æ–∑–¥–∞–Ω–∏—è –Ω–æ–≤–æ–≥–æ —Å–ø–∏—Å–∫–∞ */}
      {isCreating && (
        <form className={styles.createForm} onSubmit={handleCreateList}>
          <input
            type="text"
            className={styles.input}
            placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ —Å–ø–∏—Å–∫–∞ (–Ω–∞–ø—Ä–∏–º–µ—Ä: –û—Ç–ª–∏—á–Ω—ã–µ, –°—Ä–µ–¥–Ω–∏–µ, –û—Ç—Å—Ç–æ–π)"
            value={newListName}
            onChange={(e) => setNewListName(e.target.value)}
            autoFocus
            maxLength={50}
          />
          <div className={styles.formActions}>
            <button type="submit" className={styles.submitButton}>
              –°–æ–∑–¥–∞—Ç—å
            </button>
            <button 
              type="button" 
              className={styles.cancelButton}
              onClick={handleCancelCreate}
            >
              –û—Ç–º–µ–Ω–∞
            </button>
          </div>
        </form>
      )}

      {/* –°–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ */}
      {error && (
        <div className={styles.errorMessage}>
          {error}
        </div>
      )}

      {/* –°–ø–∏—Å–æ–∫ —Å—É—â–µ—Å—Ç–≤—É—é—â–∏—Ö —Å–ø–∏—Å–∫–æ–≤ */}
      <div className={styles.listsContainer}>
        {filteredLists.length === 0 ? (
          <div className={styles.emptyState}>
            <p>–£ –≤–∞—Å –ø–æ–∫–∞ –Ω–µ—Ç —Å–ø–∏—Å–∫–æ–≤</p>
            <p className={styles.emptyHint}>
              –°–æ–∑–¥–∞–π—Ç–µ —Å–ø–∏—Å–æ–∫, —á—Ç–æ–±—ã –æ—Ä–≥–∞–Ω–∏–∑–æ–≤–∞—Ç—å —Å–≤–æ—é –∫–æ–ª–ª–µ–∫—Ü–∏—é
            </p>
          </div>
        ) : (
          <div className={styles.listGrid}>
            {filteredLists.map((list) => (
              <div 
                key={list.id} 
                className={styles.listCard}
                onClick={() => onListSelect && onListSelect(list)}
              >
                <div className={styles.listHeader}>
                  <h3 className={styles.listName}>{list.name}</h3>
                  <button
                    className={styles.deleteButton}
                    onClick={(e) => {
                      e.stopPropagation();
                      handleDeleteList(list.id, list.name);
                    }}
                    title="–£–¥–∞–ª–∏—Ç—å —Å–ø–∏—Å–æ–∫"
                  >
                    √ó
                  </button>
                </div>
                
                <div className={styles.listInfo}>
                  <span className={styles.itemCount}>
                    {list.items?.length || 0} {' '}
                    {list.items?.length === 1 ? '—ç–ª–µ–º–µ–Ω—Ç' : 
                     list.items?.length > 1 && list.items?.length < 5 ? '—ç–ª–µ–º–µ–Ω—Ç–∞' : 
                     '—ç–ª–µ–º–µ–Ω—Ç–æ–≤'}
                  </span>
                </div>

                {/* –ü—Ä–µ–≤—å—é –ø–µ—Ä–≤—ã—Ö –ø–æ—Å—Ç–µ—Ä–æ–≤ */}
                {list.items && list.items.length > 0 && (
                  <div className={styles.previewPosters}>
                    {list.items.slice(0, 3).map((item, index) => (
                      <div key={index} className={styles.previewPoster}>
                        {item.posterPath && (
                          <img
                            src={`https://image.tmdb.org/t/p/w92${item.posterPath}`}
                            alt={item.title}
                            loading="lazy"
                          />
                        )}
                      </div>
                    ))}
                  </div>
                )}
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default CustomListManager;
